body {
  background-image: url(./bankComponent/claudio-schwarz-purzlbaum-a_DfqkONlm8-unsplash.jpg);
  background-size: cover;
  background-repeat: no-repeat no-repeat;
}

.logo {
  position: absolute;
  left: 30vw;
  top: 20vh;
  width: 10vw;
  height: 45vh;
  margin: 0;
  animation: rotation 3s infinite;
}

@keyframes rotation {
  from {
    transform: rotatey(0deg);
  }
  to {
    transform: rotatey(360deg);
  }
}
