@import url(http://fonts.googleapis.com/css?family=Ruluko);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-image: url(/Bank-konto/static/media/claudio-schwarz-purzlbaum-a_DfqkONlm8-unsplash.7eaeef39.jpg);
  background-size: cover;
  background-repeat: no-repeat no-repeat;
}

.logo {
  position: absolute;
  left: 30vw;
  top: 20vh;
  width: 10vw;
  height: 45vh;
  margin: 0;
  -webkit-animation: rotation 3s infinite;
          animation: rotation 3s infinite;
}

@-webkit-keyframes rotation {
  from {
    transform: rotatey(0deg);
  }
  to {
    transform: rotatey(360deg);
  }
}

@keyframes rotation {
  from {
    transform: rotatey(0deg);
  }
  to {
    transform: rotatey(360deg);
  }
}

.einzahlenButton {
  position: relative;
  font-size: 1.5vw;
  font-weight: bolder;
  margin: 5px;
  background-color: #fbec5d;
  color: #6b3e26;
  border-radius: 5px;
  width: 9vw;
  height: 5vh;
}
.einzahlenButton:focus {
  outline: none;
}

.auszahlenButton {
  position: relative;
  margin: 5px;
  background-color: #fbec5d;
  color: #6b3e26;
  border-radius: 5px;
  width: 9vw;
  height: 5vh;
  font-size: 1.5vw;
  font-weight: bolder;
}
.auszahlenButton:focus {
  outline: none;
}

.homepage {
  position: absolute;
  left: 50vw;
  top: 22vh;
  display: flex;
  display: grid;
  grid-template-columns: 1fr;
  width: 20vw;
  font-size: 2vw;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.705);
  border-radius: 20px;
}
h1 {
  margin: 0;
  font-family: "Ruluko", sans-serif;
  font-weight: bolder;
  font-size: 3vw;
  color: white;
  width: inherit;
}
.buttons {
  display: flex;
  justify-content: space-around;
}
.betragDisplay {
  width: inherit;
}
input {
  width: 12vw;
  margin: auto;
  text-align: center;
  font-size: 2vw;
  border-radius: 20px;
  margin-bottom: 1vw;
}

