@import url(http://fonts.googleapis.com/css?family=Ruluko);
.homepage {
  position: absolute;
  left: 50vw;
  top: 22vh;
  display: flex;
  display: grid;
  grid-template-columns: 1fr;
  width: 20vw;
  font-size: 2vw;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.705);
  border-radius: 20px;
}
h1 {
  margin: 0;
  font-family: "Ruluko", sans-serif;
  font-weight: bolder;
  font-size: 3vw;
  color: white;
  width: inherit;
}
.buttons {
  display: flex;
  justify-content: space-around;
}
.betragDisplay {
  width: inherit;
}
input {
  width: 12vw;
  margin: auto;
  text-align: center;
  font-size: 2vw;
  border-radius: 20px;
  margin-bottom: 1vw;
}
